import React from 'react';
import Cleave from 'cleave.js/react';
import './form.scss';

const InputDate = ({
  value,
  onChange,
  onFocus,
  id,
}) => (
  <div className="input_date_cont">
    <span>Cumplea√±os</span>
    <Cleave
      onChange={onChange}
      onFocus={onFocus}
      value={value}
      name="date"
      id={id}
      placeholder="DD/MM/YYYY"
      options={{ date: true, datePattern: ['d', 'm', 'Y'] }}
    />
  </div>
);

export default InputDate;
